.section-sponsors {

  .card {
    padding-bottom: 100%;
    position: relative;

    .container {
      /* Flexbox wrapper styles */
      display: flex;

      position: absolute;
      top: 0; right: 0; bottom: 0; left: 0;
      max-width: 100%;
      max-height: 100%;
    }

    .content {
      width:100%;
      /* Vertical Centering */
      -webkit-box-align: center;
      -webkit-align-items: center;
      -moz-box-align: center;
      -ms-flex-align: center;
      align-items: center;

      /* Horizontal Centering */
      -webkit-box-pack: center;
      -webkit-justify-content: center;
      -moz-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;

      margin: auto;
    }

    .table {
      display:table;
      width:100%;
      height:100%;
    }

    .table-cell {
      display:table-cell;
      vertical-align:middle
    }

    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
}
